import React from "react";

//component
import { useNavigate } from "react-router-dom";
import { Card, Badge, Container } from "react-bootstrap";
import { v4 as uuidv4 } from "uuid";
import dateFormat from "dateformat";

export default function CardsSermonPage({
  data: dataProps,
  updateData,
  onSubmit,
}) {
  const data = dataProps;
  const navigate = useNavigate();

  const sermonGoTo = (e, data) => {
    e.preventDefault();
    const dataArray = [data];
    updateData(dataArray);
    navigate("/Sermons", { state: dataArray });
  };

  return (
    <>
      <Card onClick={(e) => sermonGoTo(e, data)} style={{ cursor: "pointer" }}>
        <Card.Img
          style={{ width: "100%", height: "100%" }}
          variant="top"
          src={
            process.env.REACT_APP_API_URL +
            "/images/contents/" +
            data?.content_thumbnail
          }
        />
        <Card.Body>
          <Card.Title className="sermon-card-title">
            {data?.content_title}
          </Card.Title>
          <Container fluid className="p-0 m-0">
            {data?.content_tags?.map((tags) => {
              return (
                <Badge
                  className="pill-cursor mx-1"
                  bg="primary"
                  pill
                  key={uuidv4()}
                  onClick={() => onSubmit({ Search: tags })}
                >
                  {tags}
                </Badge>
              );
            })}
          </Container>
        </Card.Body>
        <Card.Footer>
          <small className="text-muted">
            {data?.content_tags[0]} -{" "}
            {dateFormat(data?.content_schedule, "mmmm dd, yyyy")}
          </small>
        </Card.Footer>
      </Card>
    </>
  );
}
