import React from "react";

//component
import { Container, Row, Col } from "react-bootstrap";
import CardsSermonPage from "../components/CardsSermonPage";
import { v4 as uuidv4 } from "uuid";

export default function ListSermonPage({ data, updateData, onSubmit }) {
  return (
    <>
      <Container fluid className="p-0 m-0 pt-lg-3 h-auto">
        <Row className="w-100 h-90 p-0 m-0 d-flex justify-content-center">
          <Col xs={12} lg={10}>
            <Row className="m-0 p-0 d-flex justify-content-center">
              {data?.data?.map((data) => {
                return (
                  <Col
                    lg={4}
                    md={4}
                    className="p-3 m-0 d-flex justify-content-center"
                    key={uuidv4()}
                  >
                    <CardsSermonPage
                      data={data}
                      updateData={updateData}
                      onSubmit={onSubmit}
                    />
                  </Col>
                );
              })}
            </Row>
          </Col>
        </Row>
      </Container>
    </>
  );
}
