import React from "react";

// component
import ReactPlayer from "react-player";
import { Container, Row, Col } from "react-bootstrap";
import { Puff } from "react-loader-spinner";

export default function PageSermons({ data, loading }) {
  return (
    <>
      <Container fluid className="m-0 p-0 h-100">
        <Row
          className={`align-items-center m-0 p-0 w-100 h-100 d-flex justify-content-center`}
        >
          <Col
            lg={12}
            className="m-0 p-0 d-flex justify-content-center align-items-center w-75"
          >
            {loading ? (
              <Puff
                height="80"
                width="80"
                radisu={1}
                color="rgb(255, 135, 23)"
                ariaLabel="puff-loading"
                wrapperStyle={{}}
                wrapperClass=""
                visible={true}
              />
            ) : data ? (
              <ReactPlayer
                controls={true}
                url={data?.content_url}
                width="100%"
                height="100%"
                config={{
                  facebook: {
                    attributes: {
                      style: {},
                      "data-width": "auto",
                      "data-height": "auto",
                    },
                  },
                }}
              />
            ) : (
              <>
                <Row className="h-100 d-flex justify-content-center align-items-center">
                  <Col lg={12} className="p-0 m-0 text-center">
                    <p className="empty-data-text-black p-5">No Sermon set</p>
                  </Col>
                </Row>
              </>
            )}
          </Col>
        </Row>
      </Container>
    </>
  );
}
