import React, { Fragment, useState, useEffect, useRef } from "react";

// component
import LargePageSermons from "./LargeView/PageSermons";
import SmallPageSermons from "./SmallView/PageSermons";
import ListSermonPage from "../components/ListSermonPage";
import { Container, Row, Col, Button } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import { isMobile } from "react-device-detect";
import dateFormat from "dateformat";
import { useForm } from "react-hook-form";
import { fetchData } from "../components/Functions/PageFunctions";
import { EmptySearch } from "../components/Universal/SearchBar";
import { ScaffoldComponent } from "../components/Universal/Scaffold";

export default function PageSermons() {
  const [data, setData] = useState([]);
  const location = useLocation();
  const scrollToDivider = useRef(null);
  const { register, handleSubmit } = useForm();
  const [isLoading, setIsLoading] = useState(true);
  const [dataList, setDataList] = useState([]);
  const [currentPage, setCurrentPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const itemsPerPage = 6;
  const category = "sermon";

  const setAll = (dataList, currentPage, totalPage) => {
    setDataList(dataList);
    setCurrentPage(currentPage);
    setTotalPages(totalPage);
  };

  const onSubmit = (keyword) => {
    fetchData(category, keyword.Search, currentPage, itemsPerPage).then(
      (getData) => {
        const result = getData.data.result;
        setAll(
          result,
          result.current_page,
          Math.ceil(result.total / itemsPerPage)
        );
      }
    );
  };

  const updateData = (state) => {
    setData(state[0]);
    scrollToDivider.current.scrollIntoView();
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
    fetchData(category, "", page, itemsPerPage).then((getData) => {
      const result = getData.data.result;
      setAll(
        result,
        result.current_page,
        Math.ceil(result.total / itemsPerPage)
      );
    });
  };

  useEffect(() => {
    fetchData(category, "", currentPage, itemsPerPage).then((getData) => {
      const result = getData.data.result;

      if (location?.state) {
        setData(location.state[0]);
      } else if (data?.length === 0) {
        setData(result.data[0]);
      }

      setIsLoading(false);

      setAll(
        result,
        result.current_page,
        Math.ceil(result.total / itemsPerPage)
      );
    });
  }, [currentPage, data?.length, location?.state]);

  const CardComponent = () => {
    return dataList.data?.length !== 0 ? (
      <ListSermonPage
        updateData={updateData}
        data={dataList}
        currentPage={currentPage}
        totalPages={totalPages}
        handlePageChange={handlePageChange}
        onSubmit={onSubmit}
      />
    ) : (
      <EmptySearch />
    );
  };

  const downloadButton = async (data) => {
    const href = process.env.REACT_APP_API_URL + "/files/contents/" + data;

    window.open(href);
  };

  return (
    <Fragment>
      {data ? (
        <Container fluid ref={scrollToDivider} className="m-0 p-0 w-100">
          <Row
            lg={12}
            className={`${
              isMobile ? "w-100" : "w-75"
            } page-divider m-0 p-0 h-25`}
          >
            <Col
              lg={12}
              className="h-100 align-items-center d-flex justify-content-center"
            >
              {data?.length !== 0 ? (
                <h1 className="page-divider-text sermon-top p-5">
                  {data?.content_title} -{" "}
                  {dateFormat(data?.content_schedule, "mmmm dd, yyyy")}
                </h1>
              ) : (
                <></>
              )}
            </Col>
          </Row>
        </Container>
      ) : (
        <Container fluid ref={scrollToDivider} className="m-0 p-0 w-100">
          <Row
            lg={12}
            className={`${
              isMobile ? "w-100" : "w-75"
            } page-divider m-0 p-0 h-25`}
          >
            <Col
              lg={12}
              className="h-100 align-items-center d-flex justify-content-center"
            >
              <h1 className="page-divider-text sermon-top p-5">Sermon</h1>
            </Col>
          </Row>
        </Container>
      )}

      {isMobile ? (
        <SmallPageSermons data={data} loading={isLoading} />
      ) : (
        <LargePageSermons data={data} loading={isLoading} />
      )}

      {data?.length !== 0 ? (
        <>
          {" "}
          <Container className="w-100 pt-3">
            <Row>
              <Col
                lg={12}
                className="d-flex justify-content-center text-center"
              >
                <p>
                  {data?.content_tags[0]} •{" "}
                  {dateFormat(data?.content_schedule, "mmmm dd, yyyy")} •{" "}
                  <Button
                    variant="dark"
                    href="#"
                    size="sm"
                    onClick={() => downloadButton(data?.content_files)}
                  >
                    Download Sermon File Resource
                  </Button>{" "}
                </p>
              </Col>
            </Row>
          </Container>
        </>
      ) : (
        <></>
      )}

      <Container fluid className="m-0 p-0 w-100 mt-5">
        <Row
          lg={12}
          className={`${isMobile ? "w-100" : "w-75"} page-divider m-0 p-0 h-25`}
        >
          <Col
            lg={12}
            className="h-100 align-items-center d-flex justify-content-center"
          >
            <h1 className="page-divider-text sermon-top p-5">Recent Sermons</h1>
          </Col>
        </Row>
      </Container>
      <ScaffoldComponent
        dataList={dataList}
        onSubmit={onSubmit}
        register={register}
        handleSubmit={handleSubmit}
        totalPages={totalPages}
        currentPage={currentPage}
        handlePageChange={handlePageChange}
        CardComponent={CardComponent}
      />
    </Fragment>
  );
}
