import React from "react";

// component
import ReactPlayer from "react-player";
import { Container } from "react-bootstrap";
import { Puff } from "react-loader-spinner";

export default function PageSermons({ data, loading }) {
  return (
    <>
      <Container
        fluid
        className="m-0 my-4 p-0 h-auto d-flex justify-content-center"
      >
        {loading ? (
          <Puff
            height="80"
            width="80"
            radisu={1}
            color="rgb(255, 135, 23)"
            ariaLabel="puff-loading"
            wrapperStyle={{}}
            wrapperClass=""
            visible={true}
          />
        ) : (
          <>
            {data ? (
              <ReactPlayer
                controls={true}
                url={data?.content_url}
                width="100%"
                height="100%"
                config={{
                  facebook: {
                    attributes: {
                      style: {},
                      "data-width": "auto",
                      "data-height": "auto",
                    },
                  },
                }}
              />
            ) : (
              <>
                <p className="empty-data-text-black text-center">
                  No Sermon set
                </p>
              </>
            )}
          </>
        )}
      </Container>
    </>
  );
}
