import React from "react";

//component
import { Container, Row, Col, Form, Button } from "react-bootstrap";
import { v4 as uuidv4 } from "uuid";

const SearchBar = ({ handleSubmit, onSubmit, register }) => {
  return (
    <>
      <Container fluid className="h-10 my-3">
        <Row className="d-flex justify-content-center align-items-center h-100">
          <Col xs={12} lg={8}>
            <Form className="d-flex" onSubmit={handleSubmit(onSubmit)}>
              <Form.Control
                type="text"
                placeholder="Search"
                {...register("Search", {})}
              />
              <Button variant="outline-primary" type="submit">
                Search
              </Button>
            </Form>
          </Col>
        </Row>
      </Container>
    </>
  );
};

const TagSearch = ({ dataList, onSubmit }) => {
  return (
    <>
      <Container fluid className="m-0 p-3 text-center">
        {" "}
        <Button
          size="sm"
          onClick={() => onSubmit({ Search: "" })}
          className="mx-1"
        >
          Tags: All
        </Button>
        {dataList ? (
          dataList.data_tags?.map((data) => {
            return (
              <Button
                className="mx-1"
                key={uuidv4()}
                size="sm"
                onClick={() => onSubmit({ Search: data })}
              >
                {data}
              </Button>
            );
          })
        ) : (
          <></>
        )}
      </Container>
    </>
  );
};

const EmptySearch = () => {
  return (
    <>
      <Container fluid className="m-0 p-0">
        <Row>
          <Col xs={12} lg={12} className="text-center">
            <p className="empty-result-text">(Empty Result)</p>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export { SearchBar, EmptySearch, TagSearch };
