import React from "react";

//component
import { Container, Row, Col } from "react-bootstrap";
import Pagination, {
  bootstrap5PaginationPreset,
} from "react-responsive-pagination";

export default function ListPagination({
  totalPages,
  currentPage,
  handlePageChange,
}) {
  return (
    <>
      <Container fluid className="m-0 p-0 w-100">
        <Row className="w-100 h-100 p-0 m-0 d-flex justify-content-center align-items-center">
          <Col lg={9}>
            <Pagination
              {...bootstrap5PaginationPreset} // include Bootstrap 5 preset
              total={totalPages}
              current={currentPage}
              onPageChange={(page) => handlePageChange(page)}
              extraClassName="justify-content-center"
            />
          </Col>
        </Row>
      </Container>
    </>
  );
}
