import React from "react";

import { Container } from "react-bootstrap";
import { SearchBar, TagSearch } from "./SearchBar";
import Pagination from "./ListPagination";

const ScaffoldComponent = ({
  dataList,
  onSubmit,
  register,
  handleSubmit,
  totalPages,
  currentPage,
  handlePageChange,
  CardComponent,
}) => {
  return (
    <>
      <TagSearch dataList={dataList} onSubmit={onSubmit} />
      <SearchBar
        onSubmit={onSubmit}
        register={register}
        handleSubmit={handleSubmit}
      />
      <CardComponent />
      <Pagination
        totalPages={totalPages}
        currentPage={currentPage}
        handlePageChange={handlePageChange}
      />
      <Container fluid className="m-0 p-0 my-5"></Container>
    </>
  );
};

export { ScaffoldComponent };
